
.footerContainer{
    position: fixed;
    min-width: 1100px;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: #edf2f4;
    color: black;
    z-index: 99;
}

.footerContent{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 3rem;
    width: 1000px;
    margin-inline: auto;
    padding-block: 0.5rem;
}